<template>
    <div>
                <ul style="background-color: #fff;border-top: 1px solid #eee;">
                    <li class="flex-between flex-vertical" style="border-bottom:1px solid #eee;">
                        <span class="otc-item-field flex-center">类型</span> 
                        <span class="otc-item-field2">
                            <span style="color: #00FF00;display: inline;">{{order.otcTypeStr}} </span> 
                        </span>  
                    </li>
                    <li class="flex-between flex-vertical" style="border-bottom:1px solid #eee;">
                        <span class="otc-item-field flex-center">买/卖</span> 
                        <span class="otc-item-field2">
                             <span style="color: #FF0000;display: block;clear:both;">【{{order.otcSaleorBuyStr}}】</span>
                        </span>  
                    </li> 
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">金额</span>
                        <span class="otc-item-field2">{{order.otcPrice}}</span> 
                    </li>
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">时间</span> 
                        <span class="otc-item-field2">{{order.createTime}}</span>   
                    </li>
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">状态</span> 
                        <span class="otc-item-field2">{{order.stateStr}}</span>   
                    </li>
                     <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">挂出人</span> 
                        <span class="otc-item-field2">{{order.userName}}</span>   
                    </li>
                     <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">电话</span> 
                        <span class="otc-item-field2">{{order.userMobile}}</span>   
                    </li>
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">收款信息</span> 
                        <span class="otc-item-field2">{{order.userBcusername}} - {{order.userBcnumber}} - {{order.userBcname}}</span>   
                    </li>
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">收款码</span> 
                        <span class="otc-item-field2"> 
                             <img class="pic" :src="order.userBcimg?$utils.dealImg(order.userBcimg):''" alt @click="bcImgVisible = true"/>
                        </span>   
                    </li>
                    
                     <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">说明</span> 
                        <span class="otc-item-field2">{{order.userDes}}</span>   
                    </li>
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">接单人</span> 
                        <span class="otc-item-field2">{{order.userName2}}</span>   
                    </li>
                     <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">电话</span> 
                        <span class="otc-item-field2">{{order.userMobile2}}</span>   
                    </li>
                     <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;" >
                        <span class="otc-item-field flex-center">说明</span> 
                        <span class="otc-item-field2">{{order.userDes2}}</span>   
                    </li>
                    <li  class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;">
                         <span class="otc-item-field flex-center">凭证</span> 
                         <span class="otc-item-field2">  
                             <img class="pic" :src="order.userDes2?this.$utils.dealImg(order.userDes2):''" alt @click="userDes2Visible = true"/>
                         </span>   
                    </li>
                    <li class="flex-between flex-vertical"  style="border-bottom:1px solid #eee;color:#666;">
                        <span class="otc-item-field flex-center"> </span> 
                        <span class="otc-item-field2"> </span>
                    </li>
                </ul> 
                <div class="bottom" v-if="order.otcStatus  == '0' || order.otcStatus  == '1' || order.otcStatus  == '2' || order.otcStatus  == '3' || order.otcStatus  == '7' || order.otcStatus  == '8' || order.otcStatus  == '9'">
                    <div class="btns flex">
                        <div class="btn" v-if="order.otcStatus  == '0'" @click="handle(0)">撤单</div>
                        <div class="btn" v-if="order.otcStatus  == '1' && order.userCode2 == this.selfUsercode" @click="handle(-1)">取消</div>
                        <div class="btn btnPay" v-if="order.otcStatus  == '1' && ((order.otcType == '1' && order.userCode2 == this.selfUsercode) || (order.otcType == '2' && order.userCode == this.selfUsercode))" @click="handle(1)">上传付款凭证</div>
                        <div class="btn" v-if="order.otcStatus  == '2' &&  ((order.otcType == '1' && order.userCode2 == this.selfUsercode) || (order.otcType == '2' && order.userCode == this.selfUsercode))" @click="handle(-2)">毁约</div>
                        <div class="btn btnPay" v-if="order.otcStatus  == '2' && ((order.otcType == '1' && order.userCode == this.selfUsercode) || (order.otcType == '2' && order.userCode2 == this.selfUsercode))" @click="handle(2)">转账</div> 
                        <div class="btn" v-if="order.otcStatus  == '3' && ((order.otcType == '1' && order.userCode2 == this.selfUsercode) || (order.otcType == '2' && order.userCode == this.selfUsercode))" @click="handle(3)">确认完成</div> 
                        <div class="btn" v-if="(order.otcStatus  == '7' || order.otcStatus  == '8' || order.otcStatus  == '9') && order.userCode == this.selfUsercode " @click="handle(10)">重新发布</div>
                    </div>
                </div> 
                <p v-if="!order" class="flex-center" style="font-size:12px;color:#999;height:30px;">没有数据</p>

                <!-- 上传凭证模态框 -->
                <van-popup v-model="sellModalVisible" round style="width:90%;"> 
                        <p class="flex-center" style="height:60px;font-size:16px;font-weight:700;">上传凭证</p>
                        <van-form @submit="_submit_sell">
                           <van-field label="凭证上传">
                                <template #input>
                                    <van-uploader v-model="headerFileList" max-count="1" :after-read="_read_file_finised" />
                                </template>
                            </van-field>  
                            <div style="margin: 16px;">
                                <van-button round block type="info" native-type="submit">提交</van-button>
                            </div>
                        </van-form>
                </van-popup> 
                <van-popup v-model="bcImgVisible" round style="width:90%;"> 
                    <p class="flex-center" style="height:60px;font-size:16px;font-weight:700;">查看付款码</p>
                    <p class="flex-center" style="height:200px;padding-bottom:20px;">
                        <img class="pic" :src="order.userBcimg?$utils.dealImg(order.userBcimg):''" alt />
                    </p> 
                </van-popup>
                <van-popup v-model="userDes2Visible" round style="width:90%;"> 
                    <p class="flex-center" style="height:60px;font-size:16px;font-weight:700;">查看付款凭证</p>
                    <p class="flex-center" style="height:200px;padding-bottom:20px;">
                        <img class="pic" :src="order.userDes2?$utils.dealImg(order.userDes2):''" alt />
                    </p> 
                </van-popup>
    </div> 
</template>

<script>
import { Dialog, Popup } from "vant";
import { getOTCOrderView,otccancelOrder,otccancelOrderGet,otccancelOrderPay,otcOrderPay,otcOrderComplete,otcOrderRepub,otcOrderUpdate,uploadPic } from '@/api';
export default {
    data() { 
     return {
        order: {},
        id: '',
        selfUsercode: '',
        sellModalVisible: false,
        userdes2: '',
        userBcimg: '',
        headerFileList: [{url:''}],
        bcImgVisible: false,
        userDes2Visible: false
     } 
    }, 
    mounted(){
        document.title = "OTC订单详情";
        this.selfUsercode = sessionStorage.getItem("userId") || '';
        this.loadData();
    },
    methods: {
        loadData()
        { 
            getOTCOrderView({
                otcCode: this.$route.query.id
            }).then(res=>{
                if(res.code === 0){
                    this.order = res.data;
                   
                        let stateStr = '';
                        switch(this.order.otcStatus){
                            case '0':
                                stateStr = '挂单';
                                break;
                            case '1':
                                stateStr = '接单';
                                break;
                            case '2':
                                stateStr = '已付款待转账';
                                break;
                            case '3':
                                stateStr = '已转账';
                                break;
                            case '4':
                                stateStr = '已完成';
                                break;
                            case '7':
                                stateStr = '已取消';
                                break;
                            case '8':
                                stateStr = '已取消';
                                break;
                            case '9':
                                stateStr = '已取消';
                                break;
                            default: 
                                stateStr = '';
                                break;
                        }
                        this.order.stateStr = stateStr;

                        let otcTypeStr = '';
                        switch(this.order.otcType)
                        {
                            case '1':
                                otcTypeStr = '红包';
                                break;
                            case '2':
                                otcTypeStr = '通证';
                                break;
                            default:
                                otcTypeStr = '红包';
                                break;
                        }
                        this.order.otcTypeStr = otcTypeStr;

                        let otcSaleorBuyStr = '';
                        switch(this.order.otcSaleorBuy)
                        {
                            case '1':
                                otcSaleorBuyStr = '卖';
                                break;
                            case '2':
                                otcSaleorBuyStr = '买';
                                break;
                            default:
                                otcSaleorBuyStr = '卖';
                                break;
                        }
                        this.order.otcSaleorBuyStr = otcSaleorBuyStr;  
                        this.headerFileList = [{
                            url: this.order.userDes2?this.$utils.dealImg(this.order.userDes2):''
                        }]
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        handle(type) {
            let that = this;
            switch (type) {
                case 0:
                    //取消订单
                    Dialog.confirm({
                        title: "提示",
                        message: "是否确定撤单？",
                    })
                    .then(() => {
                        // on confirm
                        that.ordercancel();
                    })
                    .catch(() => {
                    // on cancel
                    });
                    break;
                case -1:
                    //取消接单
                    Dialog.confirm({
                        title: "提示",
                        message: "是否确定取消接单？",
                    })
                    .then(() => {
                        // on confirm
                        that.ordercancel_get();
                    })
                    .catch(() => {
                        // on cancel
                    });
                    break;
                case -2:
                    //取消毁约
                    Dialog.confirm({
                        title: "提示",
                        message: "是否确定毁约？",
                    })
                    .then(() => {
                        // on confirm
                        that.ordercancel_pay();
                    })
                    .catch(() => {
                        // on cancel
                    });
                    break;
                case 1:
                    //上传凭证, 弹出弹框，上传图片，并确认。
                    this.sellModalVisible = true;
                    break;
                case 2:
                    //转账
                    Dialog.confirm({
                        title: "提示",
                        message: "是否确定转账给对方？",
                    })
                    .then(() => {
                        // on confirm
                        that.makeOrderPay();
                    })
                    .catch(() => {
                        // on cancel
                    }); 
                    break;
                case 3:
                    //完成
                    Dialog.confirm({
                        title: "提示",
                        message: "是否确定已完成？",
                    })
                    .then(() => {
                        // on confirm
                        that.makeOrderComplete();
                    })
                    .catch(() => {
                        // on cancel
                    });
                    break;
                case 10:
                    //重新发布
                    Dialog.confirm({
                        title: "提示",
                        message: "是否确定重新发布该OTC订单？",
                    })
                    .then(() => {
                        // on confirm
                        that.repubOtcOrder();
                    })
                    .catch(() => {
                        // on cancel
                    });
                    break;  
                default:
                    console.log(type);
            }
        },
        ordercancel(){
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otccancelOrder({
                id: this.order.otcCode
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("已取消");
                    this.loadData();
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });
        },
        ordercancel_get(){
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otccancelOrderGet({
                id: this.order.otcCode
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("已取消");
                    this.loadData();
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });
        },
        ordercancel_pay(){
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otccancelOrderPay({
                id: this.order.otcCode
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("已毁约");
                    this.loadData();
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });
        },
        makeOrderPay(){
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otcOrderPay({
                id: this.order.otcCode
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("已完成转账");
                    this.loadData();
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });
        },
        makeOrderComplete(){
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otcOrderComplete({
                id: this.order.otcCode
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("已确认完成");
                    this.loadData();
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });
        },
        repubOtcOrder(){
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otcOrderRepub({
                id: this.order.otcCode
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("已重新发布。");
                    this.loadData();
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });
        },
        _submit_sell(){  
            const loading = this.$toast.loading({
                duration: 0, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
            });
            otcOrderUpdate({
                otcCode: this.order.otcCode,
                userDes2: this.userdes2
            })
            .then(res => {
                loading.clear();
                if (res.code == 0) {
                    this.$toast("提交成功，请等待对方确认。");
                    this.loadData();
                    this.sellModalVisible = false;
                } else {
                    this.$toast(res.msg);
                }
            })
            .catch(err => {
                this.$vux.loading.hide();
                console.log(err);
                this.$vux.toast.show({
                    text: "网络错误",
                    type: "cancel",
                });
            });

        },
        _read_file_finised(e){
            let formData = new FormData();
            let config = {
                headers: { "Content-Type": "multipart/form-data" },
            };
            formData.append("file", e.file);
            formData.append("path", 1);
            uploadPic(formData, config).then(res => {
                if (res.code == 0) {
                    this.userdes2 = res.data.filename; 
                } else {
                    this.$toast.fail("图片上传失败！");
                }
            });
        },
    }
}
</script>

<style lang="scss" scoped>
    .otc-item-field{
        width: 25%;
        height: 50px;
        font-size: 14px;
        text-align: center;
    } 
    .otc-item-field2{
        width: 65%;
        height: 50px;
        line-height: 50px;
        font-size: 14px;
        text-align: right;
        padding-right: 10px;
    } 
    .bottom {
    position: fixed;
    bottom: 60px;
    left: 0;
    height: 50px;
    background: #fff;
    width: 100%;
    box-shadow: 0 -2px 10px 1px rgba(0, 0, 0, 0.1);
    padding: 5px 0;
    .btns {
      padding: 0 15px;
      justify-content: flex-end;
      margin-top: 9px;
      .btn {
        width: 100px;
        color: #666;
        border: 1pt solid #f4f4f4;
        float: right;
        height: 30px;
        line-height: 30px;
        border-radius: 5px;
        font-size: 13px;
        text-align: center;
        margin-left: 10px;
      }
      .btnPay {
        background: $base-color;
        color: #fff;
        border: 1pt solid $base-color;
      }
    }
  }
</style>